<template>
	<div >
		<ul>
			<li v-for="(item,index) in list" @click="more(item.id,type)">
				<span v-if="type === 'list'" >{{item.name}}</span>
				<span v-else>{{item.brand}}</span>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		props:["type"],
		data(){
			return {
				list:[]
				//,type:'list'
			}
		},
		/*created(){
	 		console.log(this.$route.params.type);
	  	},*/
		watch:{
			$route:{
				handler(n){
					//this.type = n.params.type;
					this.getData();
				},
				immediate:true
			}
		},
		/*beforeRouteEnter(to,from,next){
			//console.log("beforeRouteEnter");
			next((vm)=>{
				vm.type = vm.$route.params.type;
				vm.getData()
			});
		},
		beforeRouteUpdate(to,from,next){
			this.type = to.params.type;
			this.getData();
			//console.log(to.query)
			next();
		},
		beforeRouteLeave(to,from,next){
			if( confirm("真的要离开这个组件吗？") ){
				next();
			}
		},*/
		methods:{
			getData(){
				this.$http.get("http://localhost:3000/"+this.type).then((res)=>{
					this.list = res.data;
				})
			},
			more(id,type){
				this.$router.push({"name":"more",query:{id:id,type:type}})
				//console.log(id)
			}
		}
	}
	
</script>

<style>
	li{
		list-style: none;
		background: #cfc;
		width: 500px;
		margin: 10px auto;
		height: 30px;
		line-height: 30px;
	}
	span{
		cursor: pointer;
	}
</style>


