<template>
	<div >
		<input type="text" v-model="uname"/>
		<input type="text" v-model="upwd"/>
		<button @click="login">登录</button>
	</div>
</template>

<script>
	export default {
	data(){
		return{
			uname:"",
			upwd:""
		}
	},
	methods:{
		login(){
			if( this.uname === "wys" && this.upwd == "123" ){
				sessionStorage.setItem("user",this.uname);
				this.$router.push('/list/list');//跳到登录页
				this.$emit("name",this.uname);//子组件给父组件传值
			}
		}
	}
	}
	
</script>

<style>
</style>


