<template>
  <div id="app">
  	<div class = "hello" v-show="uname"> 你好 : {{uname}} <button @click="quit">退出</button></div>
  	<MyHeader class = "head"/>
    <router-link to = "/one">one</router-link>
    <router-link to = "/two">two</router-link>
    <!--<router-link to = "/list/list">user</router-link>
    <router-link to = "/list/computer">computer</router-link>-->
    <!--编程式导航-->
    <button @click="go('list')">用户列表</button>   
    <button @click="go('computer')">电脑列表</button>   
    <router-view @name = "receive"/>
  </div>
</template>

<script>
	import MyHeader from '@/components/MyHeader'
export default {
  name: 'App',
  components:{
  	MyHeader
  },
  data(){
  	return{
  		uname:""
  	}
  },
  methods:{
  	go(type){
  		//this.$router.push('/list/'+type);
  		//this.$router.push({"path":"/list/"+type})
  		this.$router.push({"name":"list",params:{type}}) //query:{'a':2,'b':3}}
  	},
  	receive(name){
  		console.log(name)
  		this.uname = name;
  	},
  	quit(){
  		sessionStorage.clear();
  		this.$router.push("/login");
  		this.uname = "";
  	}
  	
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#app>a,#app>button{
	display: inline-block;
	padding: 0 10px;
	height:30px;
	background: #FFFFCC;
	border: none;
	color: #666666;
	text-decoration: none;
	font-size: 18px;
	text-align: center;
	line-height: 30px;
	margin: 10px 0;
	cursor: pointer;
}
.hello{
	width: 500px;
	
	margin:0 48% ;
}
.hello button{
	background: #fff;
	cursor: pointer;
	border: none;
	color: #666666;
}
.router-link-active{
	color: red;
}
.head{
	font-size: 25px;
	font-weight: bold;
}
</style>



