<template>
	<div >
	{{title}}
	</div>
</template>

<script>
	export default{
		data(){
			return {
				title:""
			}
		},
		created(){
			
			this.$root.bus.$on("bsf",(data)=>{
				this.title  =data
			})
			console.log(this.$root.bus)
		  	this.changeTitle(this.$route.path);
				this.$router.afterEach((to,from,next)=>{  //beforeEach  要加next
					this.changeTitle(to.path);
				//	console.log(to,from)
					//next();
				})
		  },
		  
		
		methods:{
			changeTitle(path){
		  		switch(path){
		  			case '/one':this.title='one';break;
		  			case '/two/gn':this.title = 'two';break;
		  			case '/list/list':this.title = '用户列表';break;
		  			case '/list/computer':this.title = '电脑列表';break;
		  		}
		  	}
		}
	}
</script>

<style>
</style>


