<template>
	<div >
		<div v-for="item in list">
			<p v-for="(v,k,index) in item">{{k}} : {{v}}</p>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				list:[],
				type:"",
				id:""
			}
		},
		methods:{
			getData(){
				this.$http.get("http://localhost:3000/"+this.type).then((res)=>{
					this.list = res.data.filter((item)=>{
						return item.id == this.id
					});
					if( this.type == "list" ){
						console.log(this.list[0].name)
						this.$root.bus.$emit("bsf",this.list[0].name)
					}else{
						this.$root.bus.$emit("bsf",this.list[0].brand)
						
					}
					//console.log(this.list)
				})
			}
		},
		created(){
			//console.log(this.$route.query);
			this.type = this.$route.query.type;
			this.id = this.$route.query.id;
			this.getData()
		}
		
		
	}
</script>

<style>
	p{
		background: #FFCCCC;
		width: 500px;
		height:50px;
		margin: 10px auto;
		text-align: center;
		line-height: 50px;
		font-size: 20px;
		
	}
</style>


